body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.income {
  margin-left: 10px;
  margin-top: 20px;
}
.income {
  height: 100%;
}

table {
  border-collapse: collapse;
}

th, td {
  min-width: 2vw;
  max-width: 100%;
  text-align: right;
  padding: 3px;
}

table, th, td {
  border: 1px solid black;
}

.section {
  text-align: left;
}

.btn {
  margin-right: 10px;
}

.income_data {
  position: fixed;
  right: 25px;
  bottom: 75px;
}

.expenditure_data  {
  position: fixed;
  right: 25px;
  bottom: 25px;
}

.income_div {
  overflow: auto;
  height: 50vh;
  margin-right: 10px
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #888; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}
